@import url(https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,400;0,500;0,600;1,300&display=swap);
header {
    /* height: 100vh; */
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 80vh;
    position: relative;
}

/* CTA style */
.cta {
    margin-top: 2.5rem;
    display: flex;
    justify-content: center; 
    grid-gap: 1.2rem; 
    gap: 1.2rem;
}
/* Header Social Media */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
}

/* If I want to add a line under the icons */
/* .header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
} */

/* Image */
.my-photo {
    /* background: var(--color-primary); */
    /* border-radius: 10rem 10rem 10rem 10rem; */
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%; /* Move the element to the middle of the container */
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); /* Adjust the position to center it */
    margin-top: 2rem;
    overflow: hidden;
    padding: 1rem 1rem 1rem 1rem;
   
    }
   
    /* .my-photo img {
        border-radius: 10rem;
      } */

      /* Scroll Down */
      .scroll__down {
        position:absolute;
        right: -2.3rem;
        bottom: 5rem;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        font-weight: 300;
        font-size: 2rem;
      }

      /* Media Queries (Small Device) 600px or less*/
      @media screen and (max-width: 600px) {
        .header{
            height: 100vh;
        }
        
        .header__socials, /* Hide social media icons and scroll down message on phones*/
        .scroll__down{
            display: none;
        }
      }

        /* Media Queries (Medium Device) 600px to  1024*/
        @media screen and (max-width: 1024px) {
        
            .header{
                height: 68vh;
            }
        }
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    position: fixed;
    left: 50%; 
    -webkit-transform: translateX(-50%); 
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover {
    background-color:rgba(0, 0, 0, 0.3) ;
}

/* Active icon in navigation bar will have a lighter color */
nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50% ;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 1rem;
    background: linear-gradient(
        20deg,
        transparent,
        var(--color-primary),
        transparent
    );
    /* display:grid;
    place-items: center; */
}

.about__me-image {
    transition: var(--transition);
    border-radius: 4rem;
    overflow: hidden;
}

.about__me-image:hover {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

  /* Media Queries (Small Device) 600px or less*/
  @media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }

  }

    /* Media Queries (Medium Device) 600px to  1024*/
    @media screen and (max-width: 1024px) {
      .about__container {
        grid-template-columns: 1fr ;
        grid-gap: 0;
        gap: 0;
      }
        .about__me {
            width: 50%;
            margin: 2rem auto 4rem;
        }

        .about__content p {
            margin: 1rem 0 1.5rem;
        }
    }
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 1rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

.experience__container> div > h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
}

.experience__details {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
}

/* Media Queries for mobile phones (SMALL devices)*/
@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
        
    }
 
}

/* Media Queries for tablet (MEDIUM devices)*/
@media screen and (max-width: 1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }
}

.experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
}
.services__container {
  display: grid;
  /* grid-template-columns: repeat(3, 1fr);
    gap: 3rem; */
}

.service {
  background: var(--color-bg-variant);
  border-radius: 0 0 2rem 2rem;
  border: 1px solid var(--color-primary);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: var(--transition);
}

.service:hover {
  background: transparent;
  border-color: var(--color-bg-variant);
  cursor: default;
}

.service__head {
  background: var(--color-primary);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem ;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list {
    padding: 2rem;
}

.service__list li {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
    align-items: center;
}

.service__list p{
    font-size: 0.9rem;
}

/* Media Queries for mobile phones (SMALL devices)*/
@media screen and (max-width: 600px) {
 .services__container{
    grid-template-columns: 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
 }
}

/* Media Queries for tablet (MEDIUM devices)*/
@media screen and (max-width: 1024px) {
/* .service__container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
} */
}


/* So the height of the cards is the same even if one contains less info */
.service {
    height:auto; 
}




.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-bg-variant);
    background: transparent;
}

.portfolio__item-image{
    border-radius: 0.5rem;
    overflow: hidden;
    transition: var(--transition);
}

.portfolio__item:hover .portfolio__item-image {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap:1rem;
    gap:1rem;
    margin-bottom: 1rem;
}

/* Media Queries for mobile phones (SMALL devices)*/
@media screen and (max-width: 600px) {
 .portfolio__container{
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
 }
}

/* Media Queries for tablet (MEDIUM devices)*/
@media screen and (max-width: 1024px) {
.portfolio__container{
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
}
.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns:38% 58%;
    grid-gap: 12%;
    gap: 12%;
}


/* Contact Options */

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}


.contact__option:hover{
    background: transparent;
    border-color: var(--color-bg-variant);
}

.contact__option h5 {
    margin: 1.2rem 0 2rem;
}


/* Form */

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-bg-variant);
    resize: none;
}

/* Media Queries mobile phones (Small Devices) */
@media screen and (max-width: 600px) {
    .container.contact__container{
        width: var(--container-width-sm);
    }
}

/* Media Queries Tablet (Medium Devices) */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}
footer {
    background: var(--color-primary);
    padding: 10rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    color: var(--color-bg);
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
/* Variables*/

:root {
    --color-bg: #ffece8;
    --color-bg-variant: #f4e0dd;
    --color-primary: #ce6868;
    --color-primary-variant:rgba(77, 181, 255, 0.4);
    --color-white: #852323;
    --color-light:#ce6868;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

::-webkit-scrollbar {
    display: none;
}

html {
    scroll-behavior: smooth;
}

/* Body */

body {
    font-family: 'Plus Jakarta Sans', sans-serif;
    background: #ffece8;
    background: var(--color-bg);
    color:#852323;
    color:var(--color-white);
    line-height: 1.7;
    /* background-image: url("./assets/bg-image.png"); */
    /* filter: blur(5px);  */
}

/* General Style */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    /* height: 100vh; */
}

section > h2, 
section > h5 {
    text-align: center;
    color:#ce6868;
    color:var(--color-light)
}

section > h2 {
    color: #ce6868;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: #ce6868;
    color: var(--color-light);
}

a {
    color: #ce6868;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color: #852323;
    color: var(--color-white)
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #ce6868;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ce6868;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover{
    background: #852323;
    background: var(--color-white);
    color: #ffece8;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #ce6868;
    background: var(--color-primary);
    color:#ffece8;
    color:var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* Media Queries for mobile phones (SMALL devices)*/
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-ms);
    }
    section {
        margin-top: 4rem;
        margin-bottom: 4rem;
    }
    section > h2{
        margin-bottom: 2rem;
    }
}

/* Media Queries for tablet (MEDIUM devices)*/
@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
        margin-bottom: 6rem;
    }
}
